                <li><b>How to control your communications preferences:</b> You can stop receiving promotional email communications from us by clicking on the “unsubscribe link” provided in such communications. We make every effort to promptly process all unsubscribe requests. You may not opt out of service-related communications (e.g., account verification, transactional communications, changes/updates to features of the Service, technical and security notices). If you are a Job Seeker, we may also send you text message notifications related to your account and services. For example, we will send a text when an Employer sends you an interview request. By providing a mobile phone number, you authorize TalentZoom to contact you at this number using an automatic texting system to provide information and servicing regarding your account. If you no longer wish to receive SMS texts from us you can unsubscribe by replying “STOP” to one of our texts.</li>
