                <li><b>Keeping your Information safe.</b> We are committed to protecting the security of your Personal Information. Although no security system is impenetrable, we use several industry-standard security technologies and procedures to help protect your Personal Information from unauthorized access, use, or disclosure. No information is 100% secure over the Internet, thus we encourage you to never disclose or share your username and password, and to contact us immediately if you lost them or think that your account has been compromised. In the event that any information under our control is compromised as a result of a breach of security, we will take reasonable steps to investigate the situation and, where appropriate, notify those individuals whose information may have been compromised and take other steps, in accordance with any applicable laws and regulations.</li>
